$font-main: 'ProximaNova-Regular', sans-serif;
$font-bold: 'ProximaNova-Bold', sans-serif;
$accent-color:#004FEC;
$main-color: #333D51;

.blue{
    color: $accent-color;
}
.contacts{
    display: flex;
    justify-content: space-between;
    .contactsCol{
        display: flex;
        flex-direction: column;
        img{
            width: 540px;
            height: 340px;
        }
        .contactsPhone{
            color: $accent-color;
            font-size: 60px;
            line-height: 93%;
            margin-bottom: 50px;
        }
        .contactsName{
            font-size: 25px;
            line-height: 1.2em;
            position: relative;
            display: block;
            padding-left: 25px;
            margin-bottom: 25px;
        }
        .contactsName:before{
            content: '';
            width: 2px;
            height: 53px;
            background-color: $accent-color;
            position: absolute;
            top: 3px;
            left: 0;
        }
        .contactsMail{
            font-size: 20px;
            line-height: 24px;
            text-decoration: none;
            color: $main-color;
            margin-bottom: 53px;
        }
        .contactsPosition{
            font-size: 20px;
            line-height: 24px;
            color: rgba(51, 61, 81, 0.5); 
            margin-bottom: 25px;
        }
        .contactsSkills{
            font-size: 20px;
            line-height: 24px;
        }
    }
}