$font-main: 'ProximaNova-Regular', sans-serif;
$font-bold: 'ProximaNova-Bold', sans-serif;
$accent-color:#004FEC;
$main-color: #333D51;
.main{
    display: flex;
    flex-wrap: wrap;
}
.mainTitle {
    font-family: $font-main;
    font-size: 60px;
    line-height: 93%;
    color: $main-color;
    margin-bottom: 75px;
    width: 100%;
  }
  .mainTitleBlue{
    color: $accent-color;
  }
  .mainToNews{
    border: none;
    background-color: #fff;
    color: $accent-color;
    cursor: pointer;
    line-height: 24px;
    font-size: 20px;
  }
  .mainToNews:hover{
    text-decoration: underline;
  }
  
  