$font-main: 'ProximaNova-Regular', sans-serif;
$font-bold: 'ProximaNova-Bold', sans-serif;
$accent-color:#004FEC;
$main-color: #333D51;

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 120px;
    margin-bottom: 90px;
    
    a {
        font-family: $font-main;
        font-size: 18px;
        line-height: 22px;
        color: #333D51;
        text-decoration: none;
    }
    a.active{
        color: $accent-color;
    }
    ul{
        list-style: none;
        display: flex;
        justify-content: space-between;
        width: 300px;
    }
    .logo{
        font-family: $font-bold;
        font-weight: bold;
        font-size: 25px;
        line-height: 30px;
        color: #333D51;
    }
}
