$font-main: 'ProximaNova-Regular', sans-serif;
$font-bold: 'ProximaNova-Bold', sans-serif;
$accent-color:#004FEC;
$main-color: #333D51;

.newsDetail{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .newDetailCol{
        display: flex;
        flex-direction: column;
        width: 730px;
    }
    .newDetailCol:first-child{
        width: 350px;
        height: 540px;
        justify-content: space-between;
        margin-right: 30px;
    }
    img{
        max-width: 730px;
        height: 380px;
        margin-bottom: 60px;
    }
}