$main-color: #333D51;
.footer{
    margin-top: 90px;
    height: 135px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: $main-color;

    .footerInfo{
        display: flex;
        flex-direction: column;
        font-size: 12px;
        line-height: 15px;
        text-align: right;
        .bold{
            font-size: 25px;
            line-height: 30px;
            font-weight: 700;
        }
    }
}