$font-main: 'ProximaNova-Regular', sans-serif;
$font-bold: 'ProximaNova-Bold', sans-serif;
$accent-color:#004FEC;
$main-color: #333D51;

.newsItem{
    width: 350px;
    height: 290px;
    padding: 24px 25px 24px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 30px;

    .newsItemTitle{
        max-width: 300px;
        font-size: 25px;
        line-height: 30px;
        font-family: $font-main;
        color: $main-color;
        font-weight: 400;
        cursor: pointer;
        transition: 0.4s ease all;
        padding-left: 24px;
        position: relative;
        display: block;
    }
    .newsItemTitle:after{
        content: '';
        width: 2px;
        height: 90%;
        background-color: $accent-color;
        position: absolute;
        top: 5%;
        left: 0;
    }
    
    .newsItemTitle:hover{
        background-color: $accent-color;
        color: #fff;
    }
    
    
    .newsItemInfo{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-left: 24px;
    }
}